import Vue from 'vue'
import Router from 'vue-router'
/* @代表src目录 */
import Home from '@/pages/home/Home'
import City from '@/pages/city/City'
import Detail from '@/pages/detail/Detail'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/',
    name: 'Home',
    component: Home
  }, {
    path: '/city',
    name: 'City',
    component: City
  }, {
    //   detail/后面的参数会这放在id变量名里
    path: '/detail/:id',
    name: 'Detail',
    component: Detail
  }],
  // 每次页面切换都回到页面顶部
  scrollBehavior (to, from, savedPosition) {
    return {
      x: 0,
      y: 0
    }
  }
})



