<template>
<!-- ref帮帮助获取dom -->
  <div class="list" ref="wrapper">
    <div>
        <div class="area">
        <div class="title border-topbottom">当前城市</div>
        <div class="button-list">
            <div class="button-wrapper">
                <div class="button">{{this.currentCity}}</div></div>
        </div>
        </div>
        <div class="area">
        <div class="title border-topbottom">热门城市</div>
        <div class="button-list" >
         <div  @click='handleCityClick(item.name)'
         class="button-wrapper" v-for='item of hot' :key='item.id'>
            <div class="button">{{item.name}}</div>
        </div>
        </div>
        </div>
        <div class="area" v-for='(item,key) of cities' :key='key' :ref='key'>
        <div class="title border-topbottom"  >{{key}}</div>
            <div class="item-list"
            @click='handleCityClick(innerItem.name)' v-for='innerItem of item' :key="innerItem.id">
                <div class="item  border-bottom">{{innerItem.name}}</div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import { mapState,mapMutations } from 'vuex'
/* 引入框架，之后要挂载相应的对象 */
import Bscroll  from "better-scroll";
export default {
    name: "CityList",
    mounted () {
        /* 挂载scroll对象 */
      this.scroll = new Bscroll(this.$refs.wrapper)
    },
    computed: {
      ...mapState({currentCity: 'city'})
    },
    methods: {
      handleCityClick (city){
        // this.$store.commit('changeCity',city)
        console.log(city)
        this.changeCity(city)
        this.$router.push('/')
      },
      ...mapMutations(['changeCity'])
    },
    props:{
        // 子组件的属性，用来接收父组件传来的值
        hot:Array,
        cities:Object,
        letter: String
    },
    watch: {
        letter () {
            // betterScroll提供了一个自定义的方法
            // 原理就是给每个区域标记ref为key，相应字母标题区域dom到顶
            if(this.letter){
             const element = this.$refs[this.letter][0];
             this.scroll.scrollToElement(element)
             console.log(element)
            }
        }
    }
    };
</script>

<style lang="stylus" scoped>
@import '~styles/varibles.styl';
    .border-topbottom
      &:before
        border-color: #ccc
      &:after
        border-color: #ccc
    .border-bottom
      &:before
        border-color: #ccc
   
    .list
      position: absolute  
      overflow:     hidden
      top: 1.58rem
      left: 0
      right: 0
      bottom: 0
      z-index: 5
      background: #fff
      .title
        line-height: .64rem
        background: #eee
        padding-left: .2rem
        color: #666
        font-size: .26rem
    .button-list
      box-sizing:border-box
      overflow: hidden
      padding: .1rem .6rem .1rem .1rem
      .button-wrapper
        float: left 
        width: 33.33%
      .button
        margin: .1rem
        text-align: center  
        border: .02rem solid #ccc
        padding: .08rem         
    .item-list
      .item
        line-height: .70rem
        padding-left: .2rem 

      
</style>



