<template>
  <div>
    <div class="search">
      <input v-model='keyword' class="search-input" type="text" placeholder="输入城市名或拼音" />
    </div>
    <!-- 没有关键字是隐藏 -->
    <div class="search-content" 
    ref='search'
    v-show='keyword'
    >
      <ul>
        <li v-for='item of list' 
        @click='handleCityClick(item.name)'
        class="search-item border-bottom" :key='item.id'>
         {{item.name}}
        </li>
        <li class="search-item border-bottom" v-show='hasNodata'>没有该城市哦！</li>
        <!-- 尽量不要有运算 -->
      </ul>
    </div>
  </div>
</template>

<script>
import Bscroll from 'better-scroll'
import { mapMutations } from 'vuex'
export default {
  name: 'CitySearch',
  data () {
    return {
      keyword: '',
      list: [],
      timer: null
    }
  },
   methods: {
      handleCityClick (city){
        // this.$store.commit('changeCity',city)
        console.log(city)
        this.changeCity(city)
        this.$router.push('/')
      },
      ...mapMutations(['changeCity'])
    },
  computed: {
    hasNodata () {
      return !this.list.length
    }
  },
  /*监听节流keyword,*/
  watch: {
     keyword () {
      if (this.timer)clearTimeout(this.timer);
      /*如果没有输入内容为空，清空列表*/
      if(this.keyword.trim()==='')return this.list=[];

      this.timer=setTimeout(() => {
        const result= [];
        for (let i in this.cities){
          /*循环遍历每一项，将包含输入拼音或者汉的项加到数组里*/
          this.cities[i].forEach((value) => {
            if(value.spell.indexOf(this.keyword.trim()) >-1 || value.name.indexOf(this.keyword) > -1){
              result.push(value)
            }
          })
        }
        this.list = result;
      },100)
      
     }
  },
  props: { 
    cities: Object
  },    
  mounted () {
    /*让search去可以自由滑动*/
  this.scroll = new Bscroll(this.$refs.search)
  }         
//   data () { 
