//         clearTimeout(this.timer)
//       }
//       if (!this.keyword) {
//         this.list = []
//         return
//       }
//       this.timer = setTimeout(() => {
//         const result = []
//         for (let i in this.cities) {
//           this.cities[i].forEach((value) => {
//             if (value.spell.indexOf(this.keyword) > -1 || value.name.indexOf(this.keyword) > -1) {
//               result.push(value)
//             }
//           })
//         }
//         this.list = result
//       }, 100)
//     }
//   },
//   methods: {
//     handleCityClick (city) {
//       this.changeCity(city)
//       this.$router.push('/')
//     },
//     ...mapMutations(['changeCity'])
//   },
//   mounted () {
//     this.scroll = new Bscroll(this.$refs.search)
//   }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  .search
    height: .72rem
    padding: 0 .1rem
    background: $bgColor
    .search-input
      box-sizing: border-box
      width: 100%
      height: .62rem
      padding: 0 .1rem
      line-height: .62rem
      text-align: center
      border-radius: .06rem
      color: #666
  .search-content
    z-index: 10
    overflow: hidden
    position: absolute
    top: 1.58rem
    left: 0
    right: 0
    bottom: 0
    background: #eee
    .search-item
      line-height: .62rem
      padding-left: .2rem
      background: #fff
      color: #666
</style>



