<template>
  <div id="app">
      <!-- vue自带标签，路由被加载后放在内存中，就无需重复加载-->
    <!-- 加上exclude='Detail'后蓝色滚动渐隐渐现返回栏不在显示 -->
    <keep-alive >
        <!-- 名叫Detail的组件不缓存 -->
      <router-view/>
    </keep-alive>
  </div>     
</template>

<script>
export default {
  name: 'App'
}
</script>

<style></style>



