<template>
  <ul class="list">
    <li class="item"
    @touchstart.prevent='handleTouchStart' 
    @touchmove='handleTouchMove'
    @touchend='handleTouchEnd' @click='handleLetterClick'  v-for='(item) of letters' :key='item' 
    :ref='item'
    >{{item}}
    </li>
  </ul>
</template>

<script>
export default {
  name: 'CityAlphabet',
  props: {
    cities:Object
  },
  data (){
    return {
      touchStatus:false,
      startY:0,
      /*用于函数节流*/
      timer:null
    }
  },
// 此处据说为了提高性能
  updated () {
    this.startY=this.$refs['A'][0].offsetTop
  },
  computed: {
    letters () {
      const letters =[];
      // 将城市的字母名加入数组
      for(let i in this.cities){
        letters.push(i)
      }
      return letters;
    }
  },
   methods: {
    // 此处要实现兄弟组件间的传值，将字母传给城市列表,
    // 过程是，子给父，父在传给其他子
    handleLetterClick (e) {
      this.$emit('change',e.target.innerText)
       
    },
    handleTouchStart () {
       this.touchStatus=true
    },
    /*移动函数操作频率过高*/
    handleTouchMove (e) {
     if(this.touchStatus){
      if(this.timer){
        clearTimeout(this.timer)
      }
      /*通过此方法降低函数执行频率，大大降低移动事件的频率，进而提高代码性能*/
      this.timer=setTimeout(()=>{
            // 注意此处是触摸事件的e
      const touchY=e.touches[0].clientY-79;
      const index = Math.floor((touchY-this.startY)/20)
     if(index >= 0 && index < this.letters.length){
      // 在符合要求的index值范围内，向外触发事件和数据，只要给change事件一个值就可以通过之前的点击触发change事件
      this.$emit('change',this.letters[index])
     }
      },16 )
  
     }
    },
    handleTouchEnd () {
       this.touchStatus=false
    }

   }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  .list
    display: flex
    flex-direction: column
    justify-content: center
    position: absolute
    z-index: 100
    top: 1.58rem
    right: 0
    bottom: 0
    width: .4rem
    .item
      line-height: .4rem
      text-align: center
      color: $bgColor
</style>



