<template>
<swiper :options="swiperOption">
    <!-- pages是vue自带的属性 -->
<swiper-slide v-for="(page,index) of pages" :key="index">
    <!-- 这样写会导致第九个图标显示不出来，因此要使用计算属性 -->
  <div class="icons">
      <div class="icon"
       v-for='item of page' :key='item.id'>
        <div class="icon-img">
          <img class="icon-img-content" :src="item.imgUrl" alt="">
        </div>
          <p class="icon-desc">{{item.desc}}</p>
      </div>
  </div>
  </swiper-slide>
<div class="swiper-pagination" slot="pagination"></div>
  </swiper>
</template>

<script>
export default {
  name: 'HomeIcons',
  props: {
    list: Array
  },
  data () {
    return {
      /*swipper配置项*/
      swiperOption: {
        pagination: '.swiper-pagination',
        /* 支持循环轮播 */
        loop: false
      }
    }
  },
  computed: {
    /* 通过计算属性实现分页 */
    pages () {
      const pages = []
      this.list.forEach((item, index) => {
        const page = Math.floor(index / 8)
        if (!pages[page]) {
          pages[page] = []
        }
        pages[page].push(item)
      })
      return pages
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  @import '~styles/mixins.styl'
  .icons >>> .swiper-container
    height: 0
    padding-bottom: 50%
  .icons
    // margin-top: .1rem
    overflow: hidden
    height: 0
    padding-bottom: 50%
    .icon
      position: relative
      overflow: hidden
      float: left
      width: 25%
      height: 0
      padding-bottom: 25%
      .icon-img
        position: absolute
        top: 0
        left: 0
        right: 0
        bottom: .44rem
        box-sizing: border-box
        padding: .1rem
        .icon-img-content
          display: block
          margin: 0 auto
          height: 100%
      .icon-desc
        position: absolute
        left: 0
        right: 0
        bottom: 0
        height: .44rem
        line-height: .44rem
        text-align: center
        color: $darkTextColor
        /* 封装的方法 */
        ellipsis()
</style>



